.contenedorDonaciones {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 16px;
  color: #1e4050;
  text-align: center;
  gap: 20px;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  padding: 10px;
}

.donacion {
  color: black;
  width: 350px;
  max-width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.fraseDonacion {
  font-size: 25px;
  font-weight: bold;
  color: #1e4050;
  text-align: center;
}

.tipoDonacion {
  font-size: 18px;
  font-style: italic;
  color: #5893a4;
  text-align: center;
}

.textoDonacion {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px;
  color: #1e4050;
  text-align: center;
}

.boton:hover {
  background-color: red;
  color: white;
  cursor: pointer;
}

.botonPaypal {
  background-color: #ffc439;
  border: none;
  color: #034286;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 4px 0 #b38f00;
  transition: all 0.2s ease;
  text-decoration: none;
  display: inline-block;
  font-family: sans-serif;
}

.botonPaypal:hover {
  background-color: #ffb347;
  color: #0154ae;
  box-shadow: 0 2px 0 #b38f00;
  transform: translateY(1px);
}

.botonClinicas {
  background-color: #034286;
  border: none;
  color: #ffc439;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 4px 0 #b38f00;
  transition: all 0.2s ease;
}

.botonClinicas:hover {
  background-color: #0154ae;
  color: #ffb347;
  box-shadow: 0 2px 0 #b38f00;
  transform: translateY(1px);
}

/* Media query para móviles */
@media (max-width: 768px) {
  .contenedorDonaciones {
    flex-direction: column;
    align-items: center;
  }

  .donacion {
    width: 90%;
    max-width: 100%;
  }

  .botonPaypal,
  .botonClinicas {
    width: 100%;
    padding: 12px 0;
  }
}
